import React,{useState, useEffect} from 'react';
import { CssBaseline,Grid } from '@material-ui/core';
import Header from './components/Header/Header';
import List from './components/List/List';
import Map from './components/Map/Map';
import {getPlacesData} from './api';
import axios from 'axios';

const App=() =>{
    const [places,setPlaces]=useState([]);
    const[filteredPlaces,setFilteredPlaces]=useState([]);
    const [coordinates,setCoordinates]=useState({});
    const [bounds,setBounds]=useState([]);
    const[childClick,setChildClick]= useState(null);
    const[isLoading,setIsLoading]= useState(false);
    const [type,setType]=useState('restaurants');
    const [rating,setRating]=useState('');
    const [store,setStore]=useState([]);
    const [result,setResult]=useState([]);
    const [search,setSearch]=useState(""); 
    useEffect(() => {
        navigator.geolocation.getCurrentPosition(({ coords: { latitude, longitude } }) => {
          setCoordinates({ lat: latitude, lng: longitude });
        });
      }, [coordinates]);

    useEffect(()=>{
        if(bounds){
            setIsLoading(true);
        getPlacesData(type,bounds.sw,bounds.ne)
            .then((data)=>{
                setPlaces(data?.filter((place)=> place.name && place.num_reviews>0));
                setFilteredPlaces([]);
                setIsLoading(false);
            })
        }
    },[type,bounds]);

    useEffect(() => {
        if(places){
        const filteredPlaces=places.filter((place)=>Number(place.rating)>rating);
        setFilteredPlaces(filteredPlaces);
        }
      }, [places,rating]);

      useEffect(()=>{
          axios.get('https://raw.githubusercontent.com/lutangar/cities.json/master/cities.json')
          .then(({data})=>{
            setStore(data);
          })
          .catch((err)=>console.log(err));
        },[store]);

        useEffect(()=>{
          setResult(store.filter(data=>data.name.toLowerCase().includes(search.toLowerCase())));
        },[store,search]); 

        const onChange=(e)=>{
            let curr=e.target.value;
            setSearch(curr); let res=store.filter(data=>data.name.toLowerCase().includes(search.toLowerCase()))
            setResult(res);console.log(res);};
        
        const onPlaceChanged= ()=>{
            if(result.length!==0){
                const latitude=Number(result[0].lat);
                const longitude=Number(result[0].lng);
                setCoordinates({ lat: latitude, lng: longitude })
                console.log({latitude,longitude});
                console.log(coordinates);
            }
        };

      
    return (
        <>
            <CssBaseline/>
            <Header
                onChange={onChange}
                onPlaceChanged={onPlaceChanged}
                result={result}
                search={search}
            />
            <Grid container spacing={3} style={{width:'100%'}}>
                <Grid item xs={12} md={4}>
                    <List
                        places={filteredPlaces.length ? filteredPlaces : places}
                        childClick={childClick}
                        isLoading={isLoading}
                        type={type}
                        setType={setType}
                        rating={rating}
                        setRating={setRating}
                    />
                </Grid>
                <Grid item xs={12} md={8}>
                    <Map
                        setCoordinates={setCoordinates}
                        setBounds={setBounds}
                        coordinates={coordinates}
                        places={filteredPlaces.length ? filteredPlaces : places}
                        setChildClick={setChildClick}
                    />
                </Grid>
            </Grid>
        </>
    );
};

export default App;
