import React, {memo} from 'react';
import { AppBar,Toolbar,Typography,InputBase,Box, Paper } from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search'
import useStyles from './styles.js';
    const Header=({onChange,onPlaceChanged,result,search}) =>{
        const classes=useStyles();
   
    return (
        <div>
            <AppBar position='static'>
                <Toolbar className={classes.toolbar}>
                    <Typography variant="h5" className={classes.title}>
                        Travel Advisor
                    </Typography>
                    <Box display="flex">
                        <Typography variant="h6" className={classes.title}>
                            Explore New places
                        </Typography>
                        <Paper>    
                            <div>
                            <div className={classes.search}>
                                <div className={classes.searchIcon}>
                                    <SearchIcon/>
                                </div>
                                <InputBase placeholder='Search...' classes={{root:classes.inputRoot,input:classes.inputInput}} 
                                onChange={onChange}/> 
                                    <button onClick={onPlaceChanged}>Search</button>
                                    </div>
                                    {search.length!==0 && (
                                    <div className='autoC'>
                                    {
                                        result.slice(0,4).map((val,key)=>{
                                            return <div key={key}><p>{val.name}</p></div>
                                        })
                                    }
                                    </div>
                                )}    
                            </div>          
                        </Paper>
                    </Box>
                       
                </Toolbar>
            </AppBar>
          
        </div>
    );
}

export default memo(Header);
